@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;400&display=swap);
.App {
  min-height: 100%;
}

.App.dark {
  background: #333;
}
.create {
  color: #555;
  max-width: 480px;
  margin: 60px auto;
}

.create label span {
  display: block;
  margin: 30px 0 10px;
}

.create p {
  margin-top: 10px;
  font-size: 0.8em;
}

.create button {
  display: block;
  width: 100px;
  font-size: 1em;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color: #58249c;
  cursor: pointer;
  text-decoration: none;
  margin: 20px auto;
}

.ingredients {
  display: flex;
  align-items: center;
}

.ingredients button {
  margin: 0 0 0 10px;
}
.recipe-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  max-width: 1200px;
  margin: 40px auto;
}

.recipe-list .card {
  background: white;
  color: black;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  position: relative;
}

.recipe-list .card:hover {
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
}

.recipe-list .card h3 {
  color: black;
  margin-bottom: 6px;
}

.recipe-list .card p {
  color: rgb(255, 130, 80);
  font-size: 0.9em;
}

.recipe-list .card div {
  color: black;
  font-size: 0.7em;
  margin: 20px 0;
  line-height: 1.5em;
}

.recipe-list .card a {
  color: black;
  text-decoration: none;
  display: block;
  background: rgb(220, 220, 220);
  font-size: 0.9em;
  width: 100px;
  text-align: center;
  width: 120px;
  padding: 8px;
  border-radius: 4px;
  margin: 20px auto 0;
}

.error{
  color: black;
}

.recipe-list .card.dark {
  background: #555;
}

.recipe-list .card.dark a {
  background: rgb(200, 200, 200);
}

.recipe-list .card.dark h3,
.recipe-list .card.dark div
{
  color: rgb(220, 220, 220);
}

.recipe-list .card.dark p {
  color: rgb(255, 200, 0);
}

.error.dark {
  color: rgb(200, 200, 200);
}



.home {
    max-width: 1200px;
    margin: 60px auto;

}
.recipe {
    max-width: 1200px;
    margin: 40px auto;
    text-align: center;
    background: #fff;
    padding: 40px;
    box-sizing: border-box;
}

.recipe .prep {
    color: rgb(255, 130, 80);
}

.recipe h2 {
    padding: 20px;
}

.recipe ul {
    display: flex;
    padding: 0;
    justify-content: center;
    margin-top: 0;
}

.recipe li {
    list-style-type: none;
    margin-right: 10px;
}

.recipe li::after {
    content: ",";
}

.recipe li:last-child::after {
    content: ".";
}

.method {
    text-align: left;
    line-height: 1.5em;
}

.recipe.dark {
    background: #555;
    color: #e4e4e4;
}

.recipe.dark .prep {
    color: rgb(255, 200, 0);
}
.search-result h3 {
    padding-left: 10px;
    color: black;
    padding: 10px;
    max-width: 1200px;
    margin: 0 auto;
}
.search-result.dark h3 {
    color: rgb(200, 200, 200);
}

    

.searchbar form {
  display: flex;
  align-items: center;
}

.searchbar input {
  margin-left: 10px;
}
.navbar {
  padding: 10px;
  color: black;
  background: rgb(255, 200, 0);
}

.navbar nav {
  display: flex;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.navbar a.title {
  margin-right: auto;
  color: black;
  text-decoration: none;
}

.navbar a:last-child {
  color: black;
  text-decoration: none;
  margin-left: 40px;
  padding: 5px;
  border: 1px solid black;
  border-radius: 4px;
}

.navbar a:last-child:hover {
  background: white;
  color: black;
}
.theme-selector {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    max-width: 1200px;
    margin: 20px auto;
}

.mode-toggle {
    margin-right: auto;
}

.mode-toggle img {
    width: 24px;
    height: 24px;
    cursor: pointer;
}
html, body, #root {
  height: 100%;
}

body {
  font-family: 'Roboto Slab', serif;
  margin: 0;
  font-size: 1.2em;
  background: rgb(220, 220, 220);
  color: black;
}

h1, h2, h3, p {
  margin: 0;
}

input, textarea {
  color: black;
  padding: 6px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid black;
  background-color: white;
  display: block;
  width: 100%;
  box-sizing: border-box;
}

.page-title {
  text-align: center;
  margin: 40px auto;
  color: black;
}

.loading, .error {
  text-align: center;
  margin: 40px auto;
}

.dark .loading,
.dark .error 
.dark p {
  color: rgb(220, 220, 220);;
}
