.recipe {
    max-width: 1200px;
    margin: 40px auto;
    text-align: center;
    background: #fff;
    padding: 40px;
    box-sizing: border-box;
}

.recipe .prep {
    color: rgb(255, 130, 80);
}

.recipe h2 {
    padding: 20px;
}

.recipe ul {
    display: flex;
    padding: 0;
    justify-content: center;
    margin-top: 0;
}

.recipe li {
    list-style-type: none;
    margin-right: 10px;
}

.recipe li::after {
    content: ",";
}

.recipe li:last-child::after {
    content: ".";
}

.method {
    text-align: left;
    line-height: 1.5em;
}

.recipe.dark {
    background: #555;
    color: #e4e4e4;
}

.recipe.dark .prep {
    color: rgb(255, 200, 0);
}